<template>
  <dv-border-box-8
    ><div class="mydata6-box">
      <div class="data-title">
        <span style="color: #4ea4f3">▎</span>
        <span>及时取证率</span>
      </div>
      <div class="data-box" id="mydata6"></div></div
  ></dv-border-box-8>
</template>

<script>
import ChartOptiion from '../common/ChartOption'
export default {
  data() {
    return {};
  },
  mounted() {
    this.initCharts();
    window.addEventListener('resize', this.screenAdapter);
    this.screenAdapter();
  },
  destroyed() {
    window.removeEventListener('resize', this.screenAdapter);
  },
  methods: {
    initCharts() {
      const option = ChartOptiion.evidenceInTimeOption();
      this.myChart = this.$echarts.init(document.getElementById('mydata6'));
      this.myChart.setOption(option);
    },
    screenAdapter() {
      this.myChart.resize();
    },
  },
};
</script>

<style scoped lang='less'>
.mydata6-box {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .data-title {
    width: 100%;
    height: 15%;
    font-size: 1.4rem;
    color: #fff;
    padding: 6px 10px 0 10px;
    box-sizing: border-box;
    // background-color: yellow;
  }
  .data-box {
    flex: 1;
  }
}
</style>