<template>
  <dv-border-box-8
    ><div class="mydata4-box">
      <div class="data-title">
        <span style="color: #4ea4f3">▎</span>
        <span>支付方式分析</span>
      </div>
      <div class="data-box" id="mydata4"></div></div
  ></dv-border-box-8>
</template>

<script>
// import Charts from '@jiaminghi/charts';
import ChartOptiion from '../common/ChartOption'

export default {
  data() {
    return {
      myChart: null
    };
  },
  mounted() {
    this.initCharts();
    window.addEventListener('resize', this.screenAdapter);
    this.screenAdapter();
  },
  destroyed() {
    window.removeEventListener('resize', this.screenAdapter);
  },
  methods: {
    initCharts() {
      const option = ChartOptiion.payTypeOption();
      this.myChart = this.$echarts.init(document.getElementById('mydata4'));
      this.myChart.setOption(option);

      // const container = document.getElementById('mydata4');
      // const myChart = new Charts(container);
      // myChart.setOption(option1);
    },
    screenAdapter() {
      this.myChart.resize();
    },
  },
};
</script>

<style scoped lang='less'>
.mydata4-box {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .data-title {
    width: 100%;
    height: 15%;
    font-size: 1.4rem;
    color: #fff;
    padding: 6px 10px 0 10px;
    box-sizing: border-box;
    // background-color: yellow;
  }
  .data-box {
    flex: 1;
  }
}
</style>