<template>
  <div class="data-box">
    <div id="mapChart" ref="map_ref"></div>
    <!-- 近7天收入金额统计 -->
    <div class="mydata1">
      <my-datathree></my-datathree>
      <!-- <my-dataone></my-dataone> -->
    </div>
    <!-- 金额数字 -->
    <div class="mydata2">
      <my-datatwo></my-datatwo>
    </div>
    <!-- 单笔收费分布 -->
    <div class="mydata3">
      <!-- <my-datathree></my-datathree> -->
      <my-dataone></my-dataone>
    </div>
    <!-- 支付方式 -->
    <div class="mydata4">
      <my-datafour></my-datafour>
    </div>
    <!-- 路内泊位平均收益 -->
    <div class="mydata5">
      <my-datafive></my-datafive>
    </div>
    <!-- 及时取证率 -->
    <div class="mydata6">
      <my-datasix></my-datasix>
    </div>
    <!-- 应收总额排行 -->
    <div class="mydata7">
      <my-dataseven></my-dataseven>
    </div>
    <!-- 下方订单数据 -->
    <div class="mydata8">
      <my-dataeight></my-dataeight>
    </div>
  </div>
</template>

<script>
import MyDataone from '../income/MyDataOne.vue';
import MyDatatwo from '../income/MyDataTwo.vue';
import MyDatathree from '../income/MyDataThree.vue';
import MyDatafour from '../income/MyDataFour.vue'
import MyDatafive from '../income/MyDataFive.vue'
import MyDatasix from '../income/MyDataSix.vue'
import MyDataseven from '../income/MyDataSeven.vue'
import MyDataeight from '../income/MyDataEight.vue'
// import { mapActions, mapState } from 'vuex'
import * as echarts from 'echarts';
import china from '../common/china.json'

export default {
  components: {
    MyDataone,
    MyDatatwo,
    MyDatathree,
    MyDatafour,
    MyDatafive,
    MyDatasix,
    MyDataseven,
    MyDataeight
  },
  data() {
    return {
      chartInstance: null,
      myData:[{
        name:'赤水市',
        value:123
      },{
        name:'习水县',
        value:123
      }]
    };
  },
  mounted() {
    this.initChart();
    window.addEventListener('resize',this.screenAdapter);
    this.screenAdapter()
    // this.getPayPandect()
  },
  destroyed(){
    window.removeEventListener('resize', this.screenAdapter)
  },
  watch:{
    '$route':{
      handler(route){
        if(route.path === '/datadisplay/incomeanalysis'){
          this.initChart()
        }
      },
      deep:true
    }
  },
  computed: {
    // ...mapState('dataview', ['amountTotal', 'incomeTotal', 'debtTotal']),
  },
  methods: {
    // ...mapActions('dataview', ['getPayPandect']),
    initChart() {
      this.chartInstance = echarts.init(this.$refs.map_ref);
      // 通过axios获取本地的中国地图矢量数据
      console.log('china', china);
      echarts.registerMap('china', china);
      const initOption = {
        color: ['#FF9933', '#FF6600', '#FF3366'],
        tooltip:{ // 鼠标经过提示文本
          trigger:"item",
        },
        series:[
          {
            type: 'map',
            map: 'china',
            top: '5%',
            bottom: '5%',
            roam:true,
            data:this.myData,
            label:{
              show:true,
              color:'#fff'
            },
            itemStyle: {
              areaColor: '#2e72bf',
              borderColor: '#fff',
            },
          },
        ],
      };
      this.chartInstance.setOption(initOption);
    },
    screenAdapter(){
      this.chartInstance.resize()
    }
  },
};
</script>

